.senderName
{
    font-weight: 600;
    color: rgb(70, 0, 73);
}

.receivedMessage
{
    max-width: 80%;
    width: max-content;
    padding: 8px 6px;
    border-radius: 3px;
    background-color: #fee5ff;
}

.sentMessage
{
    max-width: 80%;
    width: max-content;
    padding: 8px 6px;
    border-radius: 3px;
    background-color: rgb(243, 243, 243);
    margin-left: auto;
}

.receivedMessage, .sentMessage
{
    margin-bottom: 13px;
}

#messagingBar
{
    background-color: rgb(255, 255, 255);
    padding: 10px;
    margin-top: 10px;
    border: 2px solid #ecebee;
    border-radius: 4px;
}

#messagingBar > div
{
    display: inline-block;
    width: 38%;
    margin-right: 1%;
}

#messagingBar input
{
    display: block;
    outline: none;
    border: 1px solid rgba(70, 0, 73, 0.151);
    padding: 8px 12px;
    border-radius: 3px;
    font-size: 18px;
}

#messagingBar label
{
    color: rgb(70, 0, 73);
    display: block;
    margin-bottom: 6px;
    font-size: 14px;
}

#messagingBar input:focus
{
    border-color: rgb(70, 0, 73);
}

#messagingBar [name=username]
{
    width: 100%;
    box-sizing: border-box;
    display: block;
}

#messagingBar [name=message]
{
    width: 100%;
    box-sizing: border-box;
    display: block;
}

#messagingBar div.sendButtonContainer
{
    width: 18% !important;
}

#messagingBar div.sendButtonContainer button
{
    display: block;
    outline: none;
    border: none;
    background-color: rgb(70, 0, 73);
    color: #fff;
    padding: 8px 12px;
    border-radius: 3px;
    font-size: 18px;
    cursor: pointer;
}